
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth0 } from '@auth0/auth0-react';
import { styled } from '@mui/material/styles';
import SpeedDial from '@mui/material/SpeedDial';
import SpeedDialIcon from '@mui/material/SpeedDialIcon';
import SpeedDialAction from '@mui/material/SpeedDialAction';
import HomeIcon from '@mui/icons-material/Home';
import InfoIcon from '@mui/icons-material/Info';
import SearchIcon from '@mui/icons-material/Search';
import FiberNewIcon from '@mui/icons-material/FiberNew';
import LogoutIcon from '@mui/icons-material/Logout';


export default function SpinnerMenu()
{
  const [loginLocation, setLoginLocation] = useState(window.location.origin)
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const navigate = useNavigate();
  const navigateSite = (link) =>{
    navigate(link);
  }
  const [selected, setSelected] = useState(0)
  const { logout } = useAuth0();
  return (
    <SpeedDial
          ariaLabel="SpeedDial example"
          sx={{position: 'fixed', width:'fit-content',height:'fit-content', bottom: 0,right:0}}
          icon={<SpeedDialIcon />}
          onClose={handleClose}
          onOpen={handleOpen}
          open={open}
          FabProps = {{
            sx:{
              bgcolor:'#193A6F',
              transition:'all 0.6s'
            }
          }}
        >
          <SpeedDialAction 
          key="Home"
          icon={<HomeIcon />}
          tooltipTitle="Home"
          tooltipOpen
          onClick={()=> {navigate({pathname:'/dashboard'})}}
          />
          <SpeedDialAction
            key="About"
            icon={<InfoIcon />}
            tooltipTitle="About"
            tooltipOpen
            onClick={()=> {navigate({pathname:'/dashboard/about'})}}
            />
          <SpeedDialAction
            key="Search"
            icon={<SearchIcon />}
            tooltipTitle="Search"
            tooltipOpen
            onClick={()=> {navigate({pathname:'/dashboard/search'})}}
            />
            <SpeedDialAction
            key= "New Post"
            icon={<FiberNewIcon />}
            tooltipTitle="New Post"
            tooltipOpen
            onClick={()=> {navigate({pathname:'/dashboard/newpost'})}}
            />
          <SpeedDialAction
          key = "Logout"
          icon={<LogoutIcon />}
          tooltipTitle="Logout"
          tooltipOpen
          onClick={()=> {logout({returnTo:window.location.origin})}}
          />
        </SpeedDial>
  )
}
