
import React, { useState } from 'react';
import { Auth0Provider } from '@auth0/auth0-react';
import { useNavigate } from 'react-router-dom';
import ResponsiveAppBar from './ResponsiveAppBar';
import SpinnerMenu from './SpinnerMenu';
import { Box, Button, Grid, TextField, Typography } from '@mui/material';
import {FormControl } from '@mui/material';
import { InputLabel } from '@mui/material';
import {Tooltip,Chip} from '@mui/material';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import LightTheme from './LightTheme';
import DarkTheme from './DarkTheme';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardMedia from '@mui/material/CardMedia';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import IconButton from '@mui/material/IconButton';
import FavoriteIcon from '@mui/icons-material/Favorite';
import Fade from '@mui/material/Fade';
import Modal from '@mui/material/Modal';
import Backdrop from '@mui/material/Backdrop';


export default function Search()
{
  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: '50vw',
    bgcolor: 'white',
    border: '2px solid #000',
    borderRadius: '14px',
    boxShadow: 24,
    p: 4,
  };  
  const staticTheme = DarkTheme()
  const theme = createTheme(DarkTheme())
  const [votes,setVotes] = useState([])
  const [posts,setPosts] = useState([
    {
      title: 'Ready to Search?',
      description: 'Use the top search bar to find posts!',
      tags: ['Suggestion'],
    }
  ]);
  const postBoxes = posts.map((post,index) => {
    var postTags = undefined
    var postVotes = undefined
    if(votes[index]!==undefined)
    {
      var postVotes = votes[index].votes
    }
    if(post.tags!==undefined || post.tags!==null)
    {
      postTags = post.tags?.map((tag) => {
        return (
          <Grid key={tag} item xs={12/post.tags.length}>
            <Tooltip enterTouchDelay={0} title={<h2 style={{ color: "lightblue" }}>{tag}</h2>} placement="top">
              <Chip label={tag}></Chip>
            </Tooltip>
          </Grid>
        )
      })
    }
    else
    {
      postTags = <Grid item xs={6}>
            <Tooltip enterTouchDelay={0} title={<h2 style={{ color: "lightblue" }}>No Tags</h2>} placement="top">
              <Chip label="No Tags"></Chip>
            </Tooltip>
          </Grid>
    }
    return (
      <div>
      <ThemeProvider theme={theme}>
        <Card className="searchCard" >
          <Grid container spacing={2}>
            {postTags}
          </Grid>
          <CardHeader
              sx={{color:'white'}}
            title={<h4 style={{fontWeight:"bold", bgColor:'#81adee',textAlign:'center'}}>{post.title}</h4>}
            subheader={post.date}
          />
          <CardContent>
            <Typography style={{fontSize:'1.75vh'}}>
              {post.description}
            </Typography>
          </CardContent>
          <CardActions disableSpacing>
            <IconButton aria-label="add to favorites">
              <FavoriteIcon style={staticTheme.iconColor} />
            </IconButton>
            <Typography>{postVotes}</Typography>
            <Typography style={{fontSize:'1.75vh'}} sx={{width:'100%'}}>ID: {post.identification}</Typography>
          </CardActions>
        </Card>
      </ThemeProvider>
      <br></br>
      </div>
      );
    });
  const [query,setQuery] = useState(' ');
  const [category,setCategory] = useState('title');
  const documentInput = (q,c) => 
  {
    setQuery(q);
    setCategory(c);
  } 
  const searchPosts = () =>
  {
    if(!query||!posts)
    {
      return;
    }
    fetch("https://priam-connections.netlify.app/.netlify/functions/search",
    {
      method:'POST',
      headers: {
        'Content-Type': 'text/plain'
      },
      body:JSON.stringify({
        query:query,
        category:category
      })
    })
    .then (res => res.json())
    .then((data)=> 
    {
      var idNumbers =  []
      data.map((post) => 
      {
        idNumbers.push(post.identification)
      })
      fetch("https://priam-connections.netlify.app/.netlify/functions/votes",
      {
        method:'POST',
        headers: {
          'Content-Type': 'text/plain'
        },
        body:JSON.stringify({
          idNumbers:idNumbers
        })
      }).then(res => res.json()).then((data) =>{setVotes(data)})
      if(data){
        setPosts(data);
      }
    })
  };
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const [sort,setSort] = useState('Sort')
  const handleChange = (event) => 
  {
    setCategory(event.target.value);
    event.target.label = event.target.value;
  }
  return (
    <Auth0Provider
    domain="kingtechnician.us.auth0.com"
    clientId="xTbGmROcQrGne2nH7AAEDu1C3E8hKeBW"
    >
      <ThemeProvider theme={theme}>
      <ResponsiveAppBar/>
    <div className = "App">
      <header className = "homePage App-inside-header">
        <br></br>
        <div className ="searchBox">
          <TextField value={query} sx= {{align:'left',width:'65vw',input:{color:'black'}}} id="search" label="Search" variant="filled" onChange={(e) => documentInput(e.target.value,category)} />
          <Select sx={{width:'20vw',label:{color:'black'}}} labelId="searchSelect" id="searchSelect" value={category} label="Category" onChange={handleChange}>
            <MenuItem value={'title'}>Title</MenuItem>
            <MenuItem value={'description'}>Description</MenuItem>
          </Select>
        </div>
        <br></br>
        <Button variant="contained" onClick={searchPosts}>Search</Button>
        <br></br>
        <Button variant="contained" onClick={handleOpen}>Sort</Button>
        <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <Box sx={style}>
            <br></br>
            <br></br>
            <Typography  sx={{color:'black'}} variant="h" component="h5">Sort By:</Typography>
            <br></br>
            <FormControl sx={{width:'100%'}}>
              <Select value="dateAscend" variant="outlined" size="small" fullWidth>
                <MenuItem value={"dateAscend"}>Date (Ascending)</MenuItem>
                <MenuItem value={"dateDescend"}>Date(Descending)</MenuItem>
                <MenuItem value={"upVotesAscend"}>Upvotes (Ascending)</MenuItem>
                <MenuItem value={"upVotesDescend"}>Upvotes (Descending)</MenuItem>
              </Select>
            </FormControl>
            <br></br>
            <Button variant="contained" onClick={handleClose}>Apply</Button>
          </Box>
        </Fade>
      </Modal>
        <br></br>
        {postBoxes}
        <SpinnerMenu/>
      </header>
    </div>
    </ThemeProvider>
    </Auth0Provider>
  )
}